import React, { Component } from 'react';
import {  Input, Button, Form} from 'antd';

class AddAndEditModal extends Component {
    //
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
          if (!err) {
            this.props.handleOK(values);
          }
        });
    }
    //
    cancle = () => {
        this.props.handleCancle();
    }
    render() {
        
        const formItemLayout = {
            labelCol: { span: 5 },
            wrapperCol: { span: 19 }
          };
          const tailFormItemLayout = {
            wrapperCol: { span: 24 }
          };
        const { getFieldDecorator } = this.props.form;
        let {recordInfo,modalType}=this.props
        if(recordInfo.permissionId && modalType === 'add'){
            recordInfo={}
        }
        return (
            <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                <Form.Item label="">
                    {getFieldDecorator('name', {
                        rules: [
                            {
                                required: true,
                                message: '',
                            }
                        ],
                        initialValue: recordInfo.name
                    })(<Input placeholder=""/>)}
                </Form.Item>
                <Form.Item label="">
                    {getFieldDecorator('url', {
                        initialValue: recordInfo.url
                    })(<Input placeholder=""/>)}
                </Form.Item>
                <Form.Item label="UID">
                    {getFieldDecorator('permissionId', {
                        rules: [
                            {
                                required: true,
                                message: 'id',
                            }
                        ],
                        initialValue: recordInfo.permissionId
                    })(<Input placeholder="id"/>)}
                </Form.Item>
                <Form.Item {...tailFormItemLayout} style={{textAlign:"center",marginBottom:"0"}}>
                    <Button type="primary" htmlType="submit" style={{margin:"0px 16px"}}>  </Button>
                    <Button onClick={this.cancle}>  </Button>
                </Form.Item>
            </Form>
        )
    }
}

export default Form.create()(AddAndEditModal);